html,
body,
h1,
h2,
h3,
ul,
li,
p,
button,
input,
textarea {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #3e3e3e;
}

h1,
h2,
h3 {
  color: #000;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

:focus {
  outline: none;
}

a:active {
  outline: none;
}

a:visited {
  outline: none;
}
